import React, { useEffect, useState } from 'react';
import { Box, Button, Card, CardContent, Grid, makeStyles, TextField } from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import ConsignmentService from 'src/services/consignment';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
}));

const TrackConsignment = () => {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [consignmentDetails, setConsignmentDetails] = useState([]);

  const { consignmentId } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await ConsignmentService.trackConsignmentById(id);
        setConsignmentDetails(result.content);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    consignmentId && fetchResults(consignmentId);
  }, [consignmentId, enqueueSnackbar]);

  const navigate = useNavigate();

  const renderContent = () => (
    <Grid container>
      <Grid item container xs={12}>
        {consignmentDetails.map((consignment) => (
          <Grid item container xs={12} spacing={2}>
            <Grid item xs={12}>
              <h3>Consignment Information</h3>
            </Grid>
            <Grid item xs={3}>
              consignmentNumber: {consignment.consignmentNumber}
            </Grid>
            <Grid item xs={3}>
              date: {consignment.date}
            </Grid>
            <Grid item xs={3}>
              status: {consignment.status}
            </Grid>
            <Grid item xs={6}>
              description: {consignment.description}
            </Grid>
            <Grid item xs={3}>
              jobdetail: {consignment.jobdetail}
            </Grid>
          </Grid>
        ))}
      </Grid>
    </Grid>
  );

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton title={'Track Consignment'} />
      </Box>
      <Card>
        <CardContent>{renderContent()}</CardContent>
      </Card>
    </Box>
  );
};

export default TrackConsignment;
