import React from 'react';
import { connect } from 'react-redux';
import { Navigate } from 'react-router';

const AdminPrivateRoute = ({ auth, children }) => {
  return auth && auth.isAuthenticated ? children : <Navigate to="/login" replace />;
};

const SuperAdmin = ({ auth, children }) => {
  return auth && auth.isAuthenticated && auth.user?.role === 'SuperAdmin' ? (
    children
  ) : (
    <Navigate to="/login" replace />
  );
};

const mapStateToProps = ({ auth }) => ({
  auth: auth,
});

export default connect(mapStateToProps)(AdminPrivateRoute);

export const SuperAdminRoute = connect(mapStateToProps)(SuperAdmin);
