import React from 'react';
import { TableRow, TableCell, Box, Typography } from '@material-ui/core';
import moment from 'moment';

const Cell = ({ initFreightster }) => {
  return (
    <TableRow hover key={initFreightster.id}>
      <TableCell>
        <Box alignItems="center" display="flex">
          <Typography color="textPrimary" variant="body1">
            {initFreightster.freightsterId}
          </Typography>
        </Box>
      </TableCell>

      <TableCell>{initFreightster.LoadIngPort}</TableCell>
      <TableCell>{initFreightster.weightKg}</TableCell>
      <TableCell>{initFreightster.FlightNumber}</TableCell>
      <TableCell>{moment(initFreightster?.DepartTime).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
      <TableCell>{initFreightster.total}</TableCell>
      <TableCell>{moment(initFreightster?.AScan).format('YYYY/MM/DD hh:mm:ss')}</TableCell>
      <TableCell>{initFreightster.deliveryRate}</TableCell>
    </TableRow>
  );
};

export default Cell;
