import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { Box, makeStyles, TextField } from '@material-ui/core';
import { SearchTextField } from 'src/views/utils/TextFields';
import GeneralSearchView from 'src/views/utils/GeneralSearchView';

//TODO: REMOVE THIS REPEAT COMPONENT

const useStyles = makeStyles((theme) => ({
  root: {},
}));

const Toolbar = ({
  className,
  title,
  onSearch,
  onRefresh,
  disabled = false,
  placeholder = 'Numbers',
  hideSearchText = false,
  ...rest
}) => {
  const classes = useStyles();

  const [searchText, setSearchText] = useState({
    postcodeStart: '',
    postcodeEnd: '',
    deliveryTimeStart: undefined,
    deliveryTimeEnd: undefined,
    weightStart: undefined,
    weightEnd: undefined,
  });

  const onSearchClicked = () => {
    onSearch(searchText);
  };

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <GeneralSearchView onSearch={onSearchClicked} disabled={disabled}>
        {Object.keys(searchText).map((key, index) => (
          <Box maxWidth={350} key={index}>
            <TextField
              placeholder={key}
              onChange={(e) => {
                searchText[key] = e.target.value;
                setSearchText(searchText);
              }}
              type={(index === 2 || index === 3) && 'date'}
            />
          </Box>
        ))}
      </GeneralSearchView>
    </div>
  );
};

Toolbar.propTypes = {
  className: PropTypes.string,
};

export default Toolbar;
