import React, { useEffect, useState } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  makeStyles,
  Switch,
  TextField,
  Typography,
} from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import SupplierService from 'src/services/supplier';
import { SettingService } from 'src/services/setting';
import Select from 'src/views/utils/Select';
import moment from 'moment';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
  switch: {
    margin: 'auto',
  },
}));

const CreateDelivery = () => {
  const classes = useStyles();
  const navigate = useNavigate();

  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [depotList, setDepotList] = useState([]);
  const [selectedDepot, setSelectedDepot] = useState();
  const [initialValues, setInitialValues] = useState({
    startDate: '',
    username: '',
    password: '',
    depot: '',
    accountOwner: '',
    economy: false,
    chinaPostExpress: false,
    SAMEDAY: false,
    premium: false,
    clientNumber: '',
    company: '',
    ABN: '',
    address: '',
    primaryContactPerson: '',
    primaryPhone: '',
    primaryEmail: '',
    opsContactName: '',
    opsContactPhone: '',
    opsEmail: '',
    cxContactName: '',
    cxContactPhone: '',
    cxEmail: '',
    invoiceEmail: '',
    APIToken: '',
    APIKey: '',
    note: '',
    attachments: '',
    pickupTime: '',
  });

  const { supplierId } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchDepotList = async () => {
      const depotRes = await SettingService.findDepotlist();
      setDepotList(depotRes.content);
    };

    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const [result, depotRes] = await Promise.all([
          SupplierService.findSupplierById(id),
          SettingService.findDepotlist(),
        ]);
        let newValues = initialValues;
        Object.keys(initialValues).forEach((key) => {
          newValues[key] = result.content[key];
        });
        setDepotList(depotRes.content);
        setInitialValues({
          ...newValues,
        });
        setSelectedDepot(depotRes.content.find((item) => item._id === result.content.depot));
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    supplierId ? fetchResults(supplierId) : fetchDepotList();
  }, [supplierId, enqueueSnackbar]);

  const handleSelectChange = (e) => {
    e.preventDefault();
    setSelectedDepot(e.target.value);
  };

  const requestWith = async (values, actions) => {
    try {
      await SupplierService.createSupplier({
        ...values,
        depot: selectedDepot._id,
      });

      enqueueSnackbar('Supplier Created', { variant: 'success' });

      navigate('/app/supplier');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const updateDelivery = async (values, actions, id) => {
    try {
      await SupplierService.editSupplier(supplierId, { ...values, depot: selectedDepot._id });

      enqueueSnackbar('Supplier Updated', { variant: 'success' });

      navigate('/app/supplier');
    } catch (error) {
      actions.setSubmitting(false);
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  const handleFormSubmit = (values, actions) => {
    console.log('running');
    !supplierId ? requestWith(values, actions) : updateDelivery(values, actions, supplierId);
  };

  const handleCancel = () => {
    navigate(`/app/supplier`);
  };

  const renderContent = () => (
    <Grid container>
      <Grid item container xs={12}>
        {(!supplierId || (supplierId && initialValues.username)) && (
          <Formik
            initialValues={initialValues}
            validationSchema={Yup.object().shape({
              username: Yup.string().max(255).required('*required'),
              password: Yup.string().max(255).required('*required'),
              // pickupTime: Yup.number().max(255).required('*required'),
            })}
            onSubmit={(values, actions) => {
              handleFormSubmit(values, actions);
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit} className={classes.form}>
                <Grid item container xs={12} spacing={2}>
                  {!supplierId && (
                    <Grid item xs={2}>
                      <TextField
                        error={Boolean(touched.startDate && errors.startDate)}
                        fullWidth
                        helperText={touched.startDate && errors.startDate}
                        label={'startDate'}
                        margin="normal"
                        name={'startDate'}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.startDate}
                        variant="outlined"
                        disabled={isSubmitting || loading}
                        type="date"
                      />
                    </Grid>
                  )}
                  <Grid item xs={2}>
                    <TextField
                      error={Boolean(touched.accountOwner && errors.accountOwner)}
                      fullWidth
                      helperText={touched.accountOwner && errors.accountOwner}
                      label={'accountOwner'}
                      margin="normal"
                      name={'accountOwner'}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.accountOwner}
                      variant="outlined"
                      disabled={isSubmitting || loading}
                    />
                  </Grid>
                  <Grid item xs={2}>
                    <TextField
                      error={Boolean(touched.username && errors.username)}
                      fullWidth
                      helperText={touched.username && errors.username}
                      label={'username'}
                      margin="normal"
                      name={'username'}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.username}
                      variant="outlined"
                      disabled={isSubmitting || loading}
                    />
                  </Grid>
                  <Grid item xs={2}>
                    <TextField
                      error={Boolean(touched.password && errors.password)}
                      fullWidth
                      helperText={touched.password && errors.password}
                      label={'password'}
                      margin="normal"
                      name={'password'}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.password}
                      variant="outlined"
                      disabled={isSubmitting || loading}
                      type={'password'}
                    />
                  </Grid>
                  <Grid item xs={2}>
                    <Select
                      list={depotList}
                      values={selectedDepot}
                      handleChange={handleSelectChange}
                      option="title"
                      onBlur={handleBlur}
                      disabled={loading}
                      text="DEPOT"
                    />
                  </Grid>
                  {Object.keys(initialValues)
                    .slice(5, 9)
                    .map((key) => (
                      <Grid item container xs={2} key={key}>
                        <Grid item xs={8} style={{ display: 'flex' }}>
                          <Typography style={{ margin: 'auto 0' }}>{key}</Typography>
                        </Grid>
                        <Grid item xs={4} style={{ display: 'flex' }}>
                          <Switch
                            name={key}
                            checked={values[key] || false}
                            onChange={handleChange}
                            disabled={isSubmitting || loading}
                            className={classes.switch}
                          />
                        </Grid>
                      </Grid>
                    ))}
                  {Object.keys(initialValues)
                    .slice(10, 27)
                    .map((key) => (
                      <Grid item container xs={2} key={key}>
                        <TextField
                          error={Boolean(touched[key] && errors[key])}
                          fullWidth
                          helperText={touched[key] && errors[key]}
                          label={key}
                          margin="normal"
                          name={key}
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values[key]}
                          variant="outlined"
                          disabled={isSubmitting || loading}
                        />
                      </Grid>
                    ))}

                  <Grid item xs={6}>
                    <TextField
                      error={Boolean(touched.pickupTime && errors.pickupTime)}
                      fullWidth
                      helperText={touched.pickupTime && errors.pickupTime}
                      label={
                        'pickupTime indicate that jobs can not be take until specific time, eg: a number between 1 and 24'
                      }
                      margin="normal"
                      name={'pickupTime'}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.pickupTime}
                      variant="outlined"
                      disabled={isSubmitting || loading}
                    />
                  </Grid>
                </Grid>

                <Box my={2} mx={'auto'} className={classes.buttons}>
                  <Button
                    color="primary"
                    disabled={isSubmitting || loading}
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    {supplierId ? 'Save' : 'Create'}
                  </Button>
                  <Button
                    disabled={isSubmitting || loading}
                    size="large"
                    variant="contained"
                    type="reset"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        )}
      </Grid>
    </Grid>
  );

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton title={supplierId ? 'Edit Supplier' : 'Create Supplier'} />
      </Box>
      <Card>
        <CardContent>{renderContent()}</CardContent>
      </Card>
    </Box>
  );
};

export default CreateDelivery;
