import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { Box, makeStyles, Grid } from '@material-ui/core';
import { SearchTextField } from 'src/views/utils/TextFields';
import GeneralSearchView from 'src/views/utils/GeneralSearchView';

const useStyles = makeStyles((theme) => ({
  root: {},
}));

const Toolbar = ({
  className,
  title,
  onSearch,
  onRefresh,
  disabled = false,
  placeholder = 'Numbers',
  hideSearchText = false,
  ...rest
}) => {
  const classes = useStyles();

  const [referenceNumber, setReferenceNumber] = useState('');
  const [trackingNumber, setTrackingNumber] = useState('');

  const onSearchClicked = () => {
    onSearch({ referenceNumber, trackingNumber });
  };

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <GeneralSearchView onSearch={onSearchClicked} disabled={disabled}>
        <Box maxWidth={350}>
          {!hideSearchText && (
            <SearchTextField
              placeholder={'referenceNumber'}
              onChange={setReferenceNumber}
              disabled={disabled}
            />
          )}
        </Box>
        <Box maxWidth={350}>
          {!hideSearchText && (
            <SearchTextField
              placeholder={'trackingNumber'}
              onChange={setTrackingNumber}
              disabled={disabled}
            />
          )}
        </Box>
      </GeneralSearchView>
    </div>
  );
};

Toolbar.propTypes = {
  className: PropTypes.string,
};

export default Toolbar;
