import React from 'react';
import Page from 'src/components/Page';
import CrossBorderView from './crossBorder';
import ReturnScanView from './returnScan';
import GeneralTab from '../utils/GeneralTab';
import { generalStyles } from '../utils/GeneralStyle';

const FreightsterManagementView = () => {
  const classes = generalStyles();

  return (
    <Page className={classes.noPaddingRoot} title="Freightster">
      <GeneralTab
        tabTitles={['INBOUND', 'RETURN SCAN']}
        tabLinks={['/app/inbound/scan', '/app/inbound/return']}
      >
        <CrossBorderView />
        <ReturnScanView />
      </GeneralTab>
    </Page>
  );
};

export default FreightsterManagementView;
