import React from 'react';
import moment from 'moment';
import { Button, makeStyles } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {},
  primary: {
    color: theme.palette.primary.main,
    border: `1px solid ${theme.palette.primary.main}`,
  },
  secondary: {
    color: theme.palette.tertiary.main,
    border: `1px solid ${theme.palette.tertiary.main}`,
  },
  urgent: {
    color: 'black',
    backgroundColor: 'red',
  },
  overdue: {
    color: 'black',
    backgroundColor: 'red',
  },
}));

export const MainBlueButton = ({ title, onClick, disabled = false }) => {
  return (
    <Button color="primary" variant="contained" onClick={onClick} disabled={disabled}>
      {title}
    </Button>
  );
};

export const NoBorderButton = ({ title, onClick, disabled, type }) => {
  const classes = useStyles();
  return (
    <Button onClick={onClick} disabled={disabled} className={classes[type]}>
      {title}
    </Button>
  );
};

export const StatusButton = ({ time }) => {
  const classes = useStyles();
  let type;
  const timeStamp = moment(time).valueOf();
  const currentTime = moment().valueOf();
  if (currentTime - timeStamp > 48 * 60 * 60) {
    type = 'overdue';
  } else if (currentTime - timeStamp > 24 * 60 * 60) {
    type = 'urgent';
  }
  return type ? <Button className={classes[type]}>{type}</Button> : '';
};

const MainOutLinedButton = ({ title, onClick, disabled = false, type = 'primary' }) => {
  return (
    <Button
      size="medium"
      style={{ height: 40 }}
      color={type}
      variant="outlined"
      onClick={onClick}
      disabled={disabled}
    >
      {title}
    </Button>
  );
};

export default MainOutLinedButton;
