import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, Grid, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton, StatusButton } from 'src/views/utils/Buttons';
import SupplierService from 'src/services/supplier';
import { SettingService } from 'src/services/setting';
import GeneralTable from 'src/views/utils/GeneralTable';
import Toolbar from './Toolbar';
import moment from 'moment';

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1, supplierId } = useParams();

  const [limit, setLimit] = useState(50);
  const [supplierList, setSupplierList] = useState(null);
  const [depotList, setDepotList] = useState([]);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  const [searchText, setSearchText] = useState({
    postcodeStart: undefined,
    postcodeEnd: undefined,
    deliveryTimeStart: undefined,
    deliveryTimeEnd: undefined,
    weightStart: undefined,
    weightEnd: undefined,
  });

  useEffect(() => {
    const fetchResults = async () => {
      setSupplierList(null);
      setLoading(true);
      try {
        const [result, depoRes] = await Promise.all([
          SupplierService.getjobdetailBySupplier({
            supplierId: supplierId,
            currentPage: currentPage,
            numberPerPage: limit,
            postcodeStart: Number(searchText.postcodeStart),
            postcodeEnd: Number(searchText.postcodeEnd),
            deliveryTimeStart: searchText.deliveryTimeStart,
            deliveryTimeEnd: searchText.deliveryTimeEnd,
            weightStart: Number(searchText.weightStart),
            weightEnd: Number(searchText.weightEnd),
          }),
          SettingService.findDepotlist(),
        ]);
        await SupplierService.findPackSizeRuleBySupplierId({ supplierId });
        setCount(result.content.numberOfResults);
        setSupplierList(result.content.results);
        setDepotList(depoRes.content);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, searchText, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/supplierSearch/${supplierId}/${newPage + 1}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const onSearch = ({
    postcodeStart,
    postcodeEnd,
    deliveryTimeStart,
    deliveryTimeEnd,
    weightStart,
    weightEnd,
  }) => {
    setSearchText({
      postcodeStart,
      postcodeEnd,
      deliveryTimeStart,
      deliveryTimeEnd,
      weightStart,
      weightEnd,
    });
  };

  const handleExportExcel = () => {};

  return (
    <Box>
      <Box mt={3}>
        <Toolbar onSearch={onSearch} />
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Supplier List" onRefresh={handleRefresh}>
              {' '}
              <MainOutLinedButton title="Export Result" onClick={handleExportExcel} />
            </BaseTableHeader>
          }
          headers={[
            'ID',
            'CONSIGNMENT NUMBER',
            'ONBAORD DATE',
            'COMPLETE DATE',
            'RECEIVER',
            'PHONE',
            'ADDRESS',
            'SUBURB',
            'POSTCODE',
            'STATE',
            'DISTANCE',
            'EST.TIME',
            'STATUS',
            'ACTION',
          ]}
          data={supplierList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {supplierList?.map((supplier) => (
            <TableRow hover key={supplier._id}>
              <TableCell>{supplier._id}</TableCell>
              <TableCell>{supplier.consignmentNumber}</TableCell>
              <TableCell>{moment(supplier.onboardDate).format('MM-DD-YYYY')}</TableCell>
              <TableCell>{moment(supplier.completeDate).format('MM-DD-YYYY')}</TableCell>
              <TableCell>{supplier.receiver}</TableCell>
              <TableCell>{supplier.phone}</TableCell>
              <TableCell>{supplier.address1}</TableCell>
              <TableCell>{supplier.suburb}</TableCell>
              <TableCell>{supplier.postcode}</TableCell>
              <TableCell>{supplier.state}</TableCell>
              <TableCell>{supplier.distance}</TableCell>
              <TableCell>{supplier.EstTime}</TableCell>
              <TableCell>{supplier.status}</TableCell>
              <TableCell>
                {supplier.status !== 'D' && <StatusButton time={supplier.onboardDate} />}
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
