import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { Box, makeStyles, Grid } from '@material-ui/core';
import { SearchTextField } from 'src/views/utils/TextFields';
import GeneralSearchView from 'src/views/utils/GeneralSearchView';

//TODO: REMOVE THIS REPEAT COMPONENT

const useStyles = makeStyles((theme) => ({
  root: {},
}));

const Toolbar = ({
  className,
  title,
  onSearch,
  onRefresh,
  disabled = false,
  placeholder = 'Numbers',
  hideSearchText = false,
  ...rest
}) => {
  const classes = useStyles();

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');

  const onSearchClicked = () => {
    onSearch({ name, email });
  };

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <GeneralSearchView onSearch={onSearchClicked} disabled={disabled}>
        <Box maxWidth={350}>
          {!hideSearchText && (
            <SearchTextField placeholder={'name'} onChange={setName} disabled={disabled} />
          )}
        </Box>
        <Box maxWidth={350}>
          {!hideSearchText && (
            <SearchTextField placeholder={'email'} onChange={setEmail} disabled={disabled} />
          )}
        </Box>
      </GeneralSearchView>
    </div>
  );
};

Toolbar.propTypes = {
  className: PropTypes.string,
};

export default Toolbar;
