import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import OrderSelect from 'src/views/utils/OrderSelect';
import Toolbar from '../../Toolbar';
import GeneralTable from 'src/views/utils/GeneralTable';
import FreightstersService from 'src/services/account';
// import MainOutLinedButton from 'src/views/utils/Buttons';
import Cell from './Cell';
// import BaseRouter from 'src/routes/BaseRouter';

const orderList = [
  { label: '按注册时间从近到远', value: 'TimeFarest' },
  { label: '按注册时间从远到近', value: 'TimeNearest' },
  { label: '按消费金额从高到低', value: 'AmountHighest' },
  { label: '按消费金额从低到高', value: 'AmountLowest' },
];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(50);
  const [freightsters, setFreightsters] = useState(null);
  const [count, setCount] = useState(0);
  const [refreshFlag, setRefreshFlag] = useState(false);

  const fetchResults = async () => {
    setFreightsters(null);
    try {
      const result = await FreightstersService.findFreightsters({
        currentPage: currentPage,
        numberPerPage: limit,
        country: 'AUS',
      });
      setCount(result.content.numberOfResults);
      setFreightsters(result.content.results);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    }
  };

  useEffect(() => {
    fetchResults();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/freightster/corssborder/list/${newPage + 1}`);
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader
              title="Result List"
              onRefresh={() => {
                setRefreshFlag(!refreshFlag);
              }}
            >
              {/* <OrderSelect list={orderList} onChange={setOrder} /> */}
              {/* <MainOutLinedButton
                title="Create用户"
                onClick={() => {
                  BaseRouter.createCustomerAccount({ navigate: navigate });
                }}
              /> */}
            </BaseTableHeader>
          }
          headers={[
            'Freightster Id',
            'City',
            'Weight Kg',
            'Company Name',
            'Departure Time',
            'Total',
            'A-Scan',
            'Delivered Rate',
          ]}
          data={freightsters}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
        >
          {freightsters?.map((freightster, index) => (
            <Cell initFreightster={freightster} key={index} />
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

export default Results;
