import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, Grid, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton } from 'src/views/utils/Buttons';
import SupplierService from 'src/services/supplier';
import { SettingService } from 'src/services/setting';
import GeneralTable from 'src/views/utils/GeneralTable';

// const orderList = [
// 	{ label: '按品牌名称从A到Z', value: 'nameAZ' },
// 	{ label: '按品牌名称从Z到A', value: 'nameZA' },
// 	{ label: '按编辑时间从远到近', value: 'TimeFarest' },
// 	{ label: '按编辑时间从近到远', value: 'TimeNearest' },
// ];

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1 } = useParams();

  const [limit, setLimit] = useState(50);
  const [supplierList, setSupplierList] = useState(null);
  const [depotList, setDepotList] = useState([]);
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setSupplierList(null);
      setLoading(true);
      try {
        const [result, depoRes] = await Promise.all([
          SupplierService.getAllSupplier({
            currentPage: currentPage,
            numberPerPage: limit,
          }),
          SettingService.findDepotlist(),
        ]);
        setCount(result.content.numberOfResults);
        setSupplierList(result.content.results);
        setDepotList(depoRes.content);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/supplier/list/${newPage + 1}`);
  };

  const handleEditClick = (id) => {
    navigate(`/app/supplier/edit/${id}`);
  };

  const handleFindJobs = (id) => {
    navigate(`/app/supplierSearch/${id}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const handleDeleteClick = async (id) => {
    setLoading(true);
    try {
      await SupplierService.deleteSupplier(id);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
      handleRefresh();
    }
  };

  return (
    <Box>
      <Box mt={3}>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Supplier List" onRefresh={handleRefresh}>
              <MainOutLinedButton
                title="Create Supplier"
                onClick={() => {
                  navigate(`/app/supplier/newSupplier`);
                }}
              />
            </BaseTableHeader>
          }
          headers={[
            'ID',
            'CLIENT NUMBER',
            'USERNAME',
            'COMPANY',
            'OPX EMAIL',
            'CX EMAIL',
            'FACILITY',
            'ACTION',
          ]}
          data={supplierList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {supplierList?.map((supplier) => (
            <TableRow hover key={supplier._id}>
              <TableCell>
                <Box alignItems="center" display="flex">
                  <Typography color="textPrimary" variant="body1">
                    {supplier._id}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>{supplier.username}</TableCell>
              <TableCell>{supplier.username}</TableCell>
              <TableCell>{supplier.company}</TableCell>
              <TableCell>{supplier.opsEmail}</TableCell>
              <TableCell>{supplier.cxEmail}</TableCell>
              <TableCell>{depotList.find((item) => item._id === supplier.depot)?.title}</TableCell>
              <TableCell>
                <Grid container spacing={1}>
                  <Grid item>
                    <NoBorderButton
                      title="EDIT"
                      type="primary"
                      onClick={() => handleEditClick(supplier._id)}
                    />
                  </Grid>

                  <Grid item>
                    <NoBorderButton
                      title="Find Jobs"
                      type="primary"
                      onClick={() => handleFindJobs(supplier._id)}
                    />
                  </Grid>
                  {/* TODO: BAN Supplier, Select options */}
                  {/* <Grid item>
                    <NoBorderButton
                      title="BAN"
                      type="secondary"
                      onClick={() => handleEditClick(supplier._id)}
                    />
                  </Grid> */}
                </Grid>
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
