import { authGet, authPost, authDelete, authUpdate } from './tools';

class SupplierService {
  static getAllSupplier = async ({ currentPage, numberPerPage }) => {
    const params = {
      currentPage,
      numberPerPage,
    };

    const res = await authGet('platform/supplier/findSuppliers', params);

    return res.data;
  };

  //Get a specific supplier.
  static findSupplierById = async (id) => {
    const res = await authGet(`platform/supplier/findSupplierById/${id}`);
    return res.data;
  };

  //Create a supplier.
  static createSupplier = async (params) => {
    const res = await authPost('platform/supplier/createSupplier', params);
    return res.data;
  };

  //Update a supplier.
  static editSupplier = async (supplierId, payload) => {
    const res = await authUpdate(`platform/supplier/editSupplier/${supplierId}`, payload);
    return res.data;
  };

  //Delete a named supplier.
  static deleteSupplier = async (id) => {
    const res = await authDelete(`platform/supplier/${id}`);

    return res.data;
  };

  //get job detail By Supplier
  static getjobdetailBySupplier = async ({
    supplierId,
    numberPerPage,
    currentPage,
    postcodeStart,
    postcodeEnd,
    deliveryTimeStart,
    deliveryTimeEnd,
    weightStart,
    weightEnd,
  }) => {
    const params = {
      supplierId,
      numberPerPage,
      currentPage,
      postcodeStart,
      postcodeEnd,
      deliveryTimeStart,
      deliveryTimeEnd,
      weightStart,
      weightEnd,
    };

    const res = await authPost(`platform/job/getjobdetailBySupplier`, params);

    return res.data;
  };

  //Find Pack Size Rule By SupplierId
  static findPackSizeRuleBySupplierId = async ({ supplierId }) => {
    const params = { supplierId };

    const res = await authPost(`platform/supplier/findPackSizeRuleBySupplierId`, params);

    return res.data;
  };
}

export default SupplierService;
