import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Container,
  TextField,
  Typography,
  Link,
  FormControl,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  makeStyles,
} from '@material-ui/core';
import Page from 'src/components/Page';
import AuthService from 'src/services/auth';
import MainOutLinedButton from 'src/views/utils/Buttons';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3),
  },
  margin: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(1),
  },
}));

const PasswordView = () => {
  const { enqueueSnackbar } = useSnackbar();
  const classes = useStyles();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [timeCount, setTimeCount] = useState(0);

  const updatePassword = async (value) => {
    const { account, password, smscode } = value;
    try {
      setLoading(true);
      await AuthService.forgetPassword({
        phoneNumber: account,
        newPassword: password,
        verifyCode: smscode,
      });

      enqueueSnackbar('更新密码成功', { variant: 'success' });
      navigate('/login', { replace: true });
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const handleSendSMS = async (phoneNumber) => {
    try {
      setTimeCount(30);
      await AuthService.sendSMS({ phoneNumber: phoneNumber });
      let timeValue = 29;
      const smsLock = setInterval(() => {
        if (timeValue <= 0) {
          clearInterval(smsLock);
        } else {
          timeValue -= 1;
          setTimeCount(timeValue);
        }
      }, 1000);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
    }
  };

  const PHONE_NO_REGEX = /^[0-9\- ]{8,14}$/;

  return (
    <Page className={classes.root} title="重置密码">
      <Box display="flex" flexDirection="column" height="100%" justifyContent="center">
        <Container maxWidth="sm">
          <Formik
            initialValues={{
              account: '',
              password: '',
              smscode: '',
            }}
            validationSchema={Yup.object().shape({
              account: Yup.string()
                .matches(PHONE_NO_REGEX, { message: '不是有效账号', excludeEmptyString: true })
                .required('账号是必须填写的'),
              password: Yup.string().min(6).max(255).required('密码是必须填写的'),
              confirmPassword: Yup.string()
                .oneOf([Yup.ref('password')], '密码不一致')
                .required('请确认密码'),
              smscode: Yup.string().max(255).required('验证码是必须填写的'),
            })}
            onSubmit={updatePassword}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit}>
                <Box mb={3}>
                  <Typography color="textPrimary" variant="h2">
                    重置密码
                  </Typography>
                </Box>
                <TextField
                  error={Boolean(touched.account && errors.account)}
                  fullWidth
                  helperText={touched.account && errors.account}
                  label="手机号/账号"
                  margin="normal"
                  name="account"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.account}
                  variant="outlined"
                />
                <FormControl className={classes.margin} variant="outlined" fullWidth>
                  <InputLabel
                    htmlFor="outlined-adornment-password"
                    error={Boolean(touched.smscode && errors.smscode)}
                  >
                    {Boolean(touched.smscode && errors.smscode) ? '请输入验证码' : '手机验证码'}
                  </InputLabel>
                  <OutlinedInput
                    error={Boolean(touched.smscode && errors.smscode)}
                    id="outlined-adornment-password"
                    value={values.smscode}
                    onChange={handleChange('smscode')}
                    endAdornment={
                      <InputAdornment position="end">
                        <MainOutLinedButton
                          disabled={timeCount > 0}
                          onClick={() => {
                            handleSendSMS(values.account);
                          }}
                          title={timeCount > 0 ? `发送验证码(${timeCount}s)` : '发送验证码'}
                        />
                      </InputAdornment>
                    }
                    labelWidth={80}
                  />
                </FormControl>
                <TextField
                  error={Boolean(touched.password && errors.password)}
                  fullWidth
                  helperText={touched.password && errors.password}
                  label="登录密码"
                  type={'password'}
                  margin="normal"
                  name="password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.password}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.confirmPassword && errors.confirmPassword)}
                  fullWidth
                  helperText={touched.confirmPassword && errors.confirmPassword}
                  type={'password'}
                  label="确认密码"
                  margin="normal"
                  name="confirmPassword"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.confirmPassword}
                  variant="outlined"
                />
                <Box my={2}>
                  <Button
                    color="primary"
                    disabled={isSubmitting || loading}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    确认更改
                  </Button>
                </Box>
                {/* <Typography color='textSecondary' variant='body1'>
									Don&apos;t have an account?{' '}
									<Link component={RouterLink} to='/register' variant='h6'>
										Sign up
									</Link>
								</Typography> */}
              </form>
            )}
          </Formik>
          <Link component="button" onClick={() => navigate('/login')}>
            返回登录界面
          </Link>
        </Container>
      </Box>
    </Page>
  );
};

export default PasswordView;
