import { authGet, authPost, authDelete, authUpdate } from './tools';

class ConsignmentService {
  static getAllConsignment = async ({
    currentPage,
    numberPerPage,
    referenceNumber,
    trackingNumber,
  }) => {
    const params = {
      currentPage,
      numberPerPage,
      referenceNumber,
      trackingNumber,
    };

    const res = await authGet('platform/consignment/findConsignments', params);

    return res.data;
  };

  //Get a specific consignment.
  static findConsignmentById = async (id) => {
    const res = await authGet(`platform/consignment/findConsignmentById/${id}`);
    return res.data;
  };

  //Create a consignment.
  static createConsignment = async (params) => {
    const res = await authPost('platform/consignment/createConsignment', params);
    return res.data;
  };

  //Update a consignment.
  static editConsignment = async (consignmentId, payload) => {
    const res = await authUpdate(`platform/consignment/editConsignment/${consignmentId}`, payload);
    return res.data;
  };

  //Track a specific consignment.
  static trackConsignmentById = async (id) => {
    const payload = { consignmentNumber: id };
    const res = await authPost(`platform/tracking/getTrackinginfo`, payload);
    return res.data;
  };

  //Delete a named consignment.
  static deleteConsignment = async (id) => {
    const res = await authDelete(`platform/consignment/${id}`);

    return res.data;
  };
}

export default ConsignmentService;
